/* ----------------------------------------------------------------------------------------------------------------
КНОПКИ 
------------------------------------------------------------------------------------------------------------------*/

.btn {
	display: inline-flex;
	align-items: center;

	min-height: 46px;
	min-width: 46px;
	padding: 15px;
	border-radius: 50px;
	border: 1px solid #52856a;

	font-weight: 400;
	font-size: 20px;
	text-align: center;
	transition: all 0.3s ease-in-out;
}

.btn-transparent {
	background-color: transparent;
	color: var(--black-text);
	&:hover {
		background-color: #bfd6b5;
	}
}

.btn-fill {
	background-color: #52856a;
	color: white;

	&:hover {
		background-color: #148147;
	}
}

.btn-widget {
	display: flex;
	justify-content: center;

	width: 100%;
	font-weight: 500;
	font-size: 15px;
	line-height: 100%;
	letter-spacing: 0.03em;
	color: #fff;

	min-height: 31px;
	padding: 0;
	background: #96b292;
}

.btn-like {
	width: 44px;
	height: 43px;
	background-image: url("../../icons/heart-transparentr.svg");
	background-repeat: no-repeat;
	background-size: 100% 100%;
	&.active {
		background-image: url("../../icons/heart-fill.svg");
	}
}


.btn-comment {
	display: block;
	width: 44px;
	height: 43px;
	background-image: url("../../icons/message-rectangle.svg");
	background-repeat: no-repeat;
	background-size: 100% 100%;	
}
